.groupIntroPage {
    padding-top: 1em;
    background: whitesmoke;
    height: 100%;
    overflow: scroll;
    -ms-overflow-style: none;
    text-align: left;
    padding-left: 5%;
    padding-right: 5%;
    font-size: large;

}

.groupIntroPage::-webkit-scrollbar {
    display: none;
}

h3 {
    display: inline-block;
    text-align: center;
}

h1 {
    text-align: center;
}

hr {
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
}

.center {
    text-align: center;
}