.talksLayout {
    background: whitesmoke;
    gap: 1em;
    height: 100%;
    padding: 1em;
    overflow-y: scroll;
    -ms-overflow-style: none;
}

.talksLayout::-webkit-scrollbar {
    display: none;
}

button {
    border-radius: 10px;
    padding: 5px 2em;
    border-width: 2px;
}